import React, { Component } from 'react';
import Title from '../Title';
import Item from './Item';
import style from "./style.css";
class AboutMe extends Component {
    constructor(props) {
        super(props);
        this.state = {
            title: ""
        }
    }

    render() {
        const resume = this.props.resume;
        return (
            <div className={style["about-me"]}>
                <div className={style.top}>
                    <Title title={this.state.title}/>
                </div>
                <div className={style["resume-info"]}>
                    <Item icon="user-name" label=":" value={resume.name}/>
                    <Item icon="sexm" label=":" value={resume.sex}/>
                    <Item icon="age" label=":" value={resume.age}/>
                    <Item icon="experience" label=":" value={resume.job}/>
                    <Item icon="school1" label=":" value={resume.school}/>
                    <Item icon="school" label=":" value={resume.degree}/>
                    <Item icon="date" label=":" value={resume.graduation}/>
                    <Item icon="date" label=":" value={resume.workTime}/>
                    <Item icon="phone" label=":" value={resume.phone}/>
                    <Item icon="email" label=":" value={resume.email}/>
                </div>
                
            </div>
        );
    }
}

export default AboutMe;


